/**
 * Lead Service - 
 * 
 */

import { supabase } from '../lib/supabase';

// Lead 
export type LeadStatus = 
  | 'new'           // 
  | 'contacted'     // 
  | 'qualified'     // 
  | 'scheduled'     // 
  | 'visited'       // 
  | 'negotiating'   // 
  | 'closed_won'    // 
  | 'closed_lost';  // 

// Lead 
export type LeadEventType = 
  | 'INITIAL_CONTACT'     // 
  | 'CALL_SUMMARY'        // 
  | 'LINE_MESSAGE'        // LINE 
  | 'VISIT_SCHEDULED'     // 
  | 'VISIT_COMPLETE'      // 
  | 'VISIT_CANCELLED'     // 
  | 'OFFER_SUBMITTED'     // 
  | 'OFFER_ACCEPTED'      // 
  | 'OFFER_REJECTED'      // 
  | 'CONTRACT_SIGNED'     // 
  | 'STATUS_CHANGE'       // 
  | 'NOTE_ADDED';         // 

// Lead 
export interface Lead {
  id: string;
  customer_name: string;
  customer_phone: string;
  customer_email?: string;
  customer_line_id?: string;
  agent_id: string;
  property_id: string;
  source: 'sidebar' | 'mobile_bar' | 'booking' | 'direct';
  status: LeadStatus;
  budget_range?: string;
  preferred_contact_time?: string;
  needs_description?: string;
  response_time_seconds?: number;
  first_response_at?: string;
  created_at: string;
  updated_at: string;
}

// Lead 
export interface LeadEvent {
  id: string;
  lead_id: string;
  event_type: LeadEventType;
  event_data: Record<string, unknown>;
  notes?: string;
  created_by?: string;
  created_at: string;
}

//  Lead 
export interface CreateLeadParams {
  customerName: string;
  customerPhone: string;
  customerEmail?: string;
  customerLineId?: string;
  agentId: string;
  propertyId: string;
  source: 'sidebar' | 'mobile_bar' | 'booking' | 'direct';
  budgetRange?: string;
  preferredContactTime?: string;
  needsDescription?: string;
}

//  Lead 
export interface CreateLeadResponse {
  success: boolean;
  leadId?: string;
  error?: string;
}

/**
 * 
 */
export async function createLead(params: CreateLeadParams): Promise<CreateLeadResponse> {
  try {
    //  RPC  lead
    const { data, error } = await supabase.rpc('create_lead', {
      p_customer_name: params.customerName,
      p_customer_phone: params.customerPhone,
      p_customer_email: params.customerEmail || null,
      p_customer_line_id: params.customerLineId || null,
      p_agent_id: params.agentId,
      p_property_id: params.propertyId,
      p_source: params.source,
      p_budget_range: params.budgetRange || null,
      p_preferred_contact_time: params.preferredContactTime || null,
      p_needs_description: params.needsDescription || null
    });

    if (error) {
      console.error('[LeadService] Create lead error:', error);
      return { success: false, error: error.message };
    }

    return { success: true, leadId: data };
  } catch (err) {
    const errorMessage = err instanceof Error ? err.message : '';
    console.error('[LeadService] Create lead exception:', err);
    return { success: false, error: errorMessage };
  }
}

/**
 * 
 */
export async function getLeadsForAgent(agentId: string): Promise<Lead[]> {
  try {
    const { data, error } = await supabase
      .from('leads')
      .select('*')
      .eq('agent_id', agentId)
      .order('created_at', { ascending: false });

    if (error) {
      console.error('[LeadService] Get leads error:', error);
      return [];
    }

    return data || [];
  } catch (err) {
    console.error('[LeadService] Get leads exception:', err);
    return [];
  }
}

/**
 * 
 */
export async function getLeadsForProperty(propertyId: string): Promise<Lead[]> {
  try {
    const { data, error } = await supabase
      .from('leads')
      .select('*')
      .eq('property_id', propertyId)
      .order('created_at', { ascending: false });

    if (error) {
      console.error('[LeadService] Get property leads error:', error);
      return [];
    }

    return data || [];
  } catch (err) {
    console.error('[LeadService] Get property leads exception:', err);
    return [];
  }
}

/**
 *  Lead 
 */
export async function updateLeadStatus(
  leadId: string, 
  newStatus: LeadStatus,
  notes?: string
): Promise<boolean> {
  try {
    // 
    const { error: updateError } = await supabase
      .from('leads')
      .update({ 
        status: newStatus,
        updated_at: new Date().toISOString()
      })
      .eq('id', leadId);

    if (updateError) {
      console.error('[LeadService] Update status error:', updateError);
      return false;
    }

    // 
    await addLeadEvent(leadId, 'STATUS_CHANGE', { 
      new_status: newStatus 
    }, notes);

    return true;
  } catch (err) {
    console.error('[LeadService] Update status exception:', err);
    return false;
  }
}

/**
 * 
 */
export async function recordFirstResponse(leadId: string): Promise<boolean> {
  try {
    const now = new Date().toISOString();
    
    const { error } = await supabase
      .from('leads')
      .update({ 
        first_response_at: now,
        updated_at: now
      })
      .eq('id', leadId)
      .is('first_response_at', null); // 

    if (error) {
      console.error('[LeadService] Record response error:', error);
      return false;
    }

    return true;
  } catch (err) {
    console.error('[LeadService] Record response exception:', err);
    return false;
  }
}

/**
 *  Lead 
 */
export async function addLeadEvent(
  leadId: string,
  eventType: LeadEventType,
  eventData: Record<string, unknown> = {},
  notes?: string
): Promise<boolean> {
  try {
    // 
    const { data: { user } } = await supabase.auth.getUser();

    const { error } = await supabase
      .from('lead_events')
      .insert({
        lead_id: leadId,
        event_type: eventType,
        event_data: eventData,
        notes: notes || null,
        created_by: user?.id || null
      });

    if (error) {
      console.error('[LeadService] Add event error:', error);
      return false;
    }

    return true;
  } catch (err) {
    console.error('[LeadService] Add event exception:', err);
    return false;
  }
}

/**
 *  Lead 
 */
export async function getLeadEvents(leadId: string): Promise<LeadEvent[]> {
  try {
    const { data, error } = await supabase
      .from('lead_events')
      .select('*')
      .eq('lead_id', leadId)
      .order('created_at', { ascending: true });

    if (error) {
      console.error('[LeadService] Get events error:', error);
      return [];
    }

    return data || [];
  } catch (err) {
    console.error('[LeadService] Get events exception:', err);
    return [];
  }
}

/**
 * 
 */
export async function getAgentLeadStats(agentId: string): Promise<{
  total: number;
  new: number;
  contacted: number;
  scheduled: number;
  closedWon: number;
  avgResponseTime: number | null;
}> {
  try {
    const { data, error } = await supabase
      .from('leads')
      .select('status, response_time_seconds')
      .eq('agent_id', agentId);

    if (error) {
      console.error('[LeadService] Get stats error:', error);
      return { total: 0, new: 0, contacted: 0, scheduled: 0, closedWon: 0, avgResponseTime: null };
    }

    const leads = data || [];
    const responseTimes = leads
      .map(l => l.response_time_seconds)
      .filter((t): t is number => t !== null && t !== undefined);

    return {
      total: leads.length,
      new: leads.filter(l => l.status === 'new').length,
      contacted: leads.filter(l => l.status === 'contacted').length,
      scheduled: leads.filter(l => l.status === 'scheduled').length,
      closedWon: leads.filter(l => l.status === 'closed_won').length,
      avgResponseTime: responseTimes.length > 0 
        ? Math.round(responseTimes.reduce((a, b) => a + b, 0) / responseTimes.length)
        : null
    };
  } catch (err) {
    console.error('[LeadService] Get stats exception:', err);
    return { total: 0, new: 0, contacted: 0, scheduled: 0, closedWon: 0, avgResponseTime: null };
  }
}

/**
 * 
 */
export async function recordCallSummary(
  leadId: string,
  duration: number,
  summary: string,
  outcome: 'interested' | 'follow_up' | 'not_interested'
): Promise<boolean> {
  return addLeadEvent(leadId, 'CALL_SUMMARY', {
    duration_seconds: duration,
    summary,
    outcome
  });
}

/**
 * 
 */
export async function scheduleVisit(
  leadId: string,
  visitDate: string,
  visitTime: string,
  notes?: string
): Promise<boolean> {
  // 
  const statusUpdated = await updateLeadStatus(leadId, 'scheduled');
  if (!statusUpdated) return false;

  // 
  return addLeadEvent(leadId, 'VISIT_SCHEDULED', {
    visit_date: visitDate,
    visit_time: visitTime
  }, notes);
}

/**
 * 
 */
export async function completeVisit(
  leadId: string,
  feedback: 'very_interested' | 'interested' | 'neutral' | 'not_interested',
  notes?: string
): Promise<boolean> {
  // 
  const statusUpdated = await updateLeadStatus(leadId, 'visited');
  if (!statusUpdated) return false;

  // 
  return addLeadEvent(leadId, 'VISIT_COMPLETE', {
    customer_feedback: feedback
  }, notes);
}

export const leadService = {
  createLead,
  getLeadsForAgent,
  getLeadsForProperty,
  updateLeadStatus,
  recordFirstResponse,
  addLeadEvent,
  getLeadEvents,
  getAgentLeadStats,
  recordCallSummary,
  scheduleVisit,
  completeVisit
};
