/**
 * 
 * 
 * 
 * 
 */

// 
const SENSITIVE_WORDS = [
  // 
  '', '', '', '', '', '',
  // /
  '', '', '', '',
  // 
  '', '', '',
];

// 
const AD_WORDS = [
  'LINE', '', '', '', '',
  '', 'bit.ly', 'tinyurl', '',
  '', '', '',
];

// 
const COMMUNITY_NAME_BLACKLIST = [
  '', '', '', '', '', '',
  '', '', '', '', '', '',
  '', '', '', '',
  'A', 'B', 'C', 'A', 'B', 'C',
];

export interface ContentCheckResult {
  passed: boolean;
  issues: {
    type: 'sensitive' | 'ad' | 'blacklist' | 'format';
    word?: string;
    message: string;
  }[];
}

/**
 * 
 */
export function checkContent(text: string): ContentCheckResult {
  const issues: ContentCheckResult['issues'] = [];
  const lowerText = text.toLowerCase();

  // 
  for (const word of SENSITIVE_WORDS) {
    if (text.includes(word)) {
      issues.push({
        type: 'sensitive',
        word,
        message: `${word}`,
      });
    }
  }

  // 
  for (const word of AD_WORDS) {
    if (lowerText.includes(word.toLowerCase())) {
      issues.push({
        type: 'ad',
        word,
        message: `${word}`,
      });
    }
  }

  return {
    passed: issues.length === 0,
    issues,
  };
}

/**
 * 
 */
export function checkCommunityName(name: string): ContentCheckResult {
  const issues: ContentCheckResult['issues'] = [];
  const trimmed = name.trim();

  // 
  if (trimmed.length < 2) {
    issues.push({
      type: 'format',
      message: ' 2 ',
    });
  }

  if (trimmed.length > 30) {
    issues.push({
      type: 'format',
      message: ' 30 ',
    });
  }

  // 
  for (const word of COMMUNITY_NAME_BLACKLIST) {
    if (trimmed === word || new RegExp(`^${word}$`, 'i').test(trimmed)) {
      issues.push({
        type: 'blacklist',
        word,
        message: `${word}`,
      });
    }
  }

  // +
  if (/^.*[]\d+?$/.test(trimmed) && !/|||/.test(trimmed)) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  // 
  if (/^\d+$/.test(trimmed)) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  // 
  if (/^[a-zA-Z\s]+$/.test(trimmed) && trimmed.length < 5) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  // 
  for (const word of ['', '', '', '', '', '']) {
    if (trimmed.includes(word)) {
      issues.push({
        type: 'ad',
        word,
        message: `${word}`,
      });
    }
  }

  return {
    passed: issues.length === 0,
    issues,
  };
}

/**
 * 
 */
export function checkPropertyReview(text: string, type: 'advantage' | 'disadvantage'): ContentCheckResult {
  const issues: ContentCheckResult['issues'] = [];
  const trimmed = text.trim();

  // 
  const contentCheck = checkContent(trimmed);
  issues.push(...contentCheck.issues);

  // /
  if (trimmed.length > 0 && trimmed.length < 3) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  // 
  if (/^[""'']+$/.test(trimmed)) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  // 
  if (/(.)\1{4,}/.test(trimmed)) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  return {
    passed: issues.length === 0,
    issues,
  };
}

export default {
  checkContent,
  checkCommunityName,
  checkPropertyReview,
};
