/**
 * Community Wall Service
 * 
 *  API  - 
 * 
 */

import { supabase } from '../lib/supabase';
import { communityApiBase } from '../config/env';
import type { Role } from '../types/community';

// API 
const API_BASE = communityApiBase;

//  React Query 

// Types
export interface CommunityPost {
  id: string;
  community_id: string;
  author_id: string;
  content: string;
  visibility: 'public' | 'private';
  likes_count: number;
  liked_by: string[];
  created_at: string;
  comments_count?: number;  // 
  is_pinned?: boolean;      // 
  author?: {
    name: string;
    avatar_url?: string;
    role?: 'resident' | 'agent' | 'member' | 'official';
    floor?: string;         // 
  };
}

export interface CommunityReview {
  id: string;
  community_id: string;
  author_id: string;
  content: {
    pros: string[];
    cons: string;
    property_title?: string;
  };
  created_at: string;
  agent?: {                   // 
    name: string;
    company?: string;
    stats?: {
      visits: number;         // 
      deals: number;          // 
    };
  };
}

export interface CommunityQuestion {
  id: string;
  community_id: string;
  author_id: string;
  question: string;
  answers: {
    id: string;
    author_id: string;
    content: string;
    is_expert: boolean;
    created_at: string;
    author?: {                // 
        name: string;
        role?: 'resident' | 'member' | 'agent' | 'official';
    };
  }[];
  created_at: string;
  /** API  */
  hasMoreAnswers?: boolean;
  /** API  */
  totalAnswers?: number;
  /**  */
  answers_count?: number;
}

export interface CommunityWallData {
  communityInfo?: {           // 
    name: string;
    year: number;
    units: number;
    managementFee: number;
    builder: string;
    members?: number;
    avgRating?: number;
    monthlyInteractions?: number;
    forSale?: number;
  };
  posts: {
    public: CommunityPost[];
    private: CommunityPost[];
    publicTotal: number;
    privateTotal: number;
  };
  reviews: {
    items: CommunityReview[];
    total: number;
  };
  questions: {
    items: CommunityQuestion[];
    total: number;
  };
  isAuthenticated?: boolean;
  viewerRole?: Role;
}

//  auth token
async function getAuthToken(): Promise<string | null> {
  const { data: { session } } = await supabase.auth.getSession();
  return session?.access_token ?? null;
}

//  fetch 
async function fetchAPI<T>(
  endpoint: string,
  options: RequestInit = {}
): Promise<T> {
  const token = await getAuthToken();
  
  const headers: HeadersInit = {
    'Content-Type': 'application/json',
    ...(token && { 'Authorization': `Bearer ${token}` }),
    ...options.headers,
  };

  const response = await fetch(`${API_BASE}${endpoint}`, {
    ...options,
    headers,
  });

  if (!response.ok) {
    const errorData = await response.json().catch(() => ({ error: '' }));
    // 
    let errorMessage = '';
    if (typeof errorData.error === 'string') {
      errorMessage = errorData.error;
    } else if (errorData.error?.message) {
      errorMessage = errorData.error.message;
    } else if (errorData.message) {
      errorMessage = errorData.message;
    } else if (Array.isArray(errorData.error)) {
      // Zod  array
      errorMessage = errorData.error.map((e: any) => e.message || e).join(', ');
    }
    
    const error = new Error(errorMessage || `HTTP ${response.status}`);
    (error as any).status = response.status;
    (error as any).code = errorData.code;
    throw error;
  }

  return response.json();
}

/**
 * 
 *  React Query  fetch
 */
export async function getCommunityWall(
  communityId: string,
  options: { 
    includePrivate?: boolean;
  } = {}
): Promise<CommunityWallData> {
  const { includePrivate = false } = options;
  const includePrivateParam = includePrivate ? '1' : '0';
  return fetchAPI<CommunityWallData>(
    `/wall?communityId=${communityId}&type=all&includePrivate=${includePrivateParam}`
  );
}

/**
 * 
 */
export async function getPublicPosts(
  communityId: string,
  options: { page?: number; limit?: number } = {}
): Promise<{ items: CommunityPost[]; total: number }> {
  const { page = 1, limit = 20 } = options;
  const offset = (page - 1) * limit;
  
  return fetchAPI(`/wall?communityId=${communityId}&type=posts&visibility=public&offset=${offset}&limit=${limit}`);
}

/**
 * 
 */
export async function getPrivatePosts(
  communityId: string,
  options: { page?: number; limit?: number } = {}
): Promise<{ items: CommunityPost[]; total: number }> {
  const { page = 1, limit = 20 } = options;
  const offset = (page - 1) * limit;
  
  return fetchAPI(`/wall?communityId=${communityId}&type=posts&visibility=private&offset=${offset}&limit=${limit}`);
}

/**
 *  properties 
 *  React Query 
 */
export async function getReviews(
  communityId: string
): Promise<{ items: CommunityReview[]; total: number }> {
  return fetchAPI<{ items: CommunityReview[]; total: number }>(
    `/wall?communityId=${communityId}&type=reviews`
  );
}

/**
 * 
 */
export async function getQuestions(
  communityId: string
): Promise<{ items: CommunityQuestion[]; total: number }> {
  return fetchAPI<{ items: CommunityQuestion[]; total: number }>(
    `/wall?communityId=${communityId}&type=questions`
  );
}

/**
 * 
 *  React Query invalidateQueries 
 */
export async function createPost(
  communityId: string,
  content: string,
  visibility: 'public' | 'private' = 'public'
): Promise<CommunityPost> {
  return fetchAPI('/post', {
    method: 'POST',
    body: JSON.stringify({ communityId, content, visibility }),
  });
}

/**
 * /
 */
export async function toggleLike(
  postId: string
): Promise<{ liked: boolean; likes_count: number }> {
  return fetchAPI('/like', {
    method: 'POST',
    body: JSON.stringify({ postId }),
  });
}

/**
 * 
 *  React Query invalidateQueries 
 */
export async function askQuestion(
  communityId: string,
  question: string
): Promise<CommunityQuestion> {
  return fetchAPI('/question', {
    method: 'POST',
    body: JSON.stringify({ communityId, question }),
  });
}

/**
 * 
 * API  POST + action=answer PUT
 */
export async function answerQuestion(
  questionId: string,
  content: string
): Promise<{ id: string; content: string }> {
  return fetchAPI('/question', {
    method: 'POST',
    body: JSON.stringify({ action: 'answer', questionId, content }),
  });
}

/**
 * 
 *  React Query  invalidateQueries
 */
export function clearCommunityCache(_communityId?: string): void {
  //  -  React Query 
}

export default {
  getCommunityWall,
  getPublicPosts,
  getPrivatePosts,
  getReviews,
  getQuestions,
  createPost,
  toggleLike,
  askQuestion,
  answerQuestion,
  clearCommunityCache,
};
