import React, { useMemo, useState } from "react";
import { Events, track } from "../analytics/track";

function calcMonthlyPayment(principal: number, annualRate: number, years: number) {
  const r = annualRate / 12 / 100;
  const n = years * 12;
  if (r === 0) return principal / n;
  return (principal * r) / (1 - Math.pow(1 + r, -n));
}

export const BudgetLite: React.FC = () => {
  const [total, setTotal] = useState<number>(1500);
  const [down, setDown] = useState<number>(30);
  const [rate, setRate] = useState<number>(2.25);
  const [years, setYears] = useState<number>(20);
  const [income, setIncome] = useState<number | "">("");

  const principal = useMemo(() => Math.max(0, total * (1 - down / 100)), [total, down]);
  const monthly = useMemo(() => Math.round(calcMonthlyPayment(principal * 10000, rate, years)), [principal, rate, years]); // 
  const mgmt = 3000; // 

  const burden = useMemo(() => {
    if (typeof income !== "number" || income <= 0) return null;
    return Math.round((monthly + mgmt) / income * 100);
  }, [income, monthly]);

  const onCopy = () => {
    const text = `\n${total}${down}%  ${principal.toFixed(0)}${years}${rate}%   ${monthly}  ${mgmt} ` + (burden !== null ? `\n ${burden}%` : "");
    navigator.clipboard.writeText(text).then(() => track(Events.BudgetCopy, {}));
  };

  const warmTip = (() => {
    if (burden === null) return "";
    if (burden >= 50) return "";
    if (burden >= 35) return "/";
    return "";
  })();

  return (
    <div style={{ border: "1px solid #eee", borderRadius: 12, padding: 12, background: "#fff" }}>
      <div style={{ fontWeight: 600, marginBottom: 8 }}>Lite</div>
      <div style={{ display: "grid", gridTemplateColumns: "repeat(5,1fr)", gap: 8 }}>
        <label>()<input type="number" value={total} onChange={e => setTotal(Number(e.target.value))} style={{ width: "100%", padding: 6, borderRadius: 8, border: "1px solid #ddd" }} /></label>
        <label>(%)<input type="number" value={down} onChange={e => setDown(Number(e.target.value))} style={{ width: "100%", padding: 6, borderRadius: 8, border: "1px solid #ddd" }} /></label>
        <label>(%)<input type="number" value={rate} onChange={e => setRate(Number(e.target.value))} style={{ width: "100%", padding: 6, borderRadius: 8, border: "1px solid #ddd" }} /></label>
        <label><input type="number" value={years} onChange={e => setYears(Number(e.target.value))} style={{ width: "100%", padding: 6, borderRadius: 8, border: "1px solid #ddd" }} /></label>
        <label>()<input type="number" value={typeof income === "number" ? income : ""} onChange={e => { const v = Number(e.target.value); setIncome(Number.isFinite(v) ? v : ""); }} style={{ width: "100%", padding: 6, borderRadius: 8, border: "1px solid #ddd" }} /></label>
      </div>
      <div style={{ marginTop: 10, fontSize: 14, color: "#0a2246" }}>
         <b>{monthly.toLocaleString()}</b>  <b>{principal.toFixed(0)}</b> 
        {burden !== null && <> <b>{burden}%</b></>}
      </div>
      <div style={{ marginTop: 8, color: "#6b7280" }}>{warmTip}</div>
      <button onClick={onCopy} style={{ marginTop: 8, padding: "6px 10px", borderRadius: 8, border: "1px solid #1749D7", background: "#fff", color: "#1749D7" }}></button>
    </div>
  );
};
