// 

export type ReportStyle = 'simple' | 'investment' | 'marketing';

export interface ReportHighlight {
  id: string;
  icon: string;
  title: string;
  subtitle: string;
  description: string;
}

export interface ReportData {
  id: string;
  shortCode: string;
  propertyId: string;
  agentId: string;
  
  // 
  style: ReportStyle;
  highlights: string[]; // highlight ids
  photos: string[];
  customMessage?: string;
  
  // 
  viewCount: number;
  createdAt: string;
  expiresAt: string;
}

export interface PropertyReportData {
  // 
  id: string;
  publicId: string;
  title: string;
  price: number;
  address: string;
  description: string;
  images: string[];
  
  // 
  size?: number;
  age?: number;
  rooms?: number;
  halls?: number;
  bathrooms?: number;
  floorCurrent?: string;
  floorTotal?: number;
  propertyType?: string;
  direction?: string;
  parking?: string;
  
  // 
  communityName?: string;
  communityYear?: number;
  communityUnits?: number;
  managementFee?: number;
  
  // 
  advantage1?: string;
  advantage2?: string;
  disadvantage?: string;
  
  // 
  agent: {
    id: string;
    name: string;
    avatarUrl: string;
    company: string;
    phone?: string;
    lineId?: string;
    trustScore?: number;
    reviewCount?: number;
    experience?: number;
  };
}

