/**
 * LockedOverlay Component
 * 
 *  +  CTA 
 * 
 */

import type { ReactNode } from 'react';

interface LockedOverlayProps {
  /**  */
  children: ReactNode;
  /**  5  */
  hiddenCount: number;
  /**  */
  countLabel: string;
  /**  */
  benefits?: string[];
  /** CTA  */
  ctaText?: string;
  /** CTA  */
  onCtaClick?: () => void;
  /**  CTA  */
  showCta?: boolean;
  /**  */
  visible?: boolean;
}

export function LockedOverlay({
  children,
  hiddenCount,
  countLabel,
  benefits = ['', ''],
  ctaText = ' / ',
  onCtaClick,
  showCta = true,
  visible = true,
}: LockedOverlayProps) {
  // 
  if (hiddenCount <= 0) {
    return null;
  }

  //  CSS transition /
  const isHidden = !visible;

  return (
    <div 
      className={`relative transition-opacity duration-200 ${
        isHidden ? 'pointer-events-none opacity-0' : 'opacity-100'
      }`}
      aria-hidden={isHidden}
    >
      {/*  */}
      <div className="pointer-events-none select-none blur-sm" aria-hidden="true">
        {children}
      </div>
      
      {/*  */}
      <div className="absolute inset-0 flex flex-col items-center justify-center rounded-[14px] bg-white/85 p-5 text-center">
        <h4 className="mb-1 text-sm font-extrabold text-brand-700">
            {hiddenCount} {countLabel}
        </h4>
        <p className="mb-2.5 text-xs text-ink-600">
          {benefits.map((b, i) => (
            <span key={i}>
              {i > 0 && ''} {b}
            </span>
          ))}
        </p>
        {showCta && (
          <button 
            onClick={onCtaClick}
            className="rounded-full bg-gradient-to-br from-brand to-brand-600 px-6 py-2.5 text-[13px] font-bold text-white transition-transform hover:scale-[1.02]"
          >
            {ctaText}
          </button>
        )}
      </div>
    </div>
  );
}
