import React, { useState, useRef } from 'react';
import { notify } from '../../../../lib/notify';
import { 
  Upload, Copy, ChevronRight, ChevronLeft,
  Home, MapPin, Ruler, Calendar, Building, Compass, DollarSign,
  MessageCircle, CheckCircle, Sparkles, X, ExternalLink
} from 'lucide-react';
import uagStyles from '../../UAG.module.css';
import styles from './ReportGenerator.module.css';
import type { Listing } from '../../types/uag.types';

// 
type ReportStyle = 'simple' | 'investment' | 'marketing';

// Props 
interface ReportGeneratorProps {
  listings?: Listing[];
}

// 
interface Highlight {
  id: string;
  icon: string;
  title: string;
  description: string;
  selected: boolean;
}

// 
interface PropertyData {
  id: string;
  title: string;
  address: string;
  district: string;
  price: number;
  pricePerPing: number;
  size: number;
  rooms: string;
  floor: string;
  age: number;
  direction: string;
  parking: string;
  managementFee: number;
  community: string;
  communityYear: number;
  communityUnits: number;
  images: string[];
  highlights: Highlight[];
}

// 
const DEFAULT_PROPERTY: PropertyData = {
  id: 'demo-001',
  title: ' 12F322',
  address: ' 168 ',
  district: '',
  price: 32880000,
  pricePerPing: 521000,
  size: 67.3,
  rooms: '322',
  floor: '12F/15F',
  age: 5,
  direction: '',
  parking: '',
  managementFee: 3500,
  community: '',
  communityYear: 2019,
  communityUnits: 280,
  images: [],
  highlights: [
    { id: 'commute', icon: '', title: '', description: ' 5 ', selected: true },
    { id: 'school', icon: '', title: '', description: '', selected: true },
    { id: 'community', icon: '', title: '', description: '', selected: true },
    { id: 'view', icon: '', title: '', description: '', selected: false },
    { id: 'amenity', icon: '', title: '', description: '7-11  3 ', selected: false },
    { id: 'parking', icon: '', title: '', description: '', selected: false },
    { id: 'renovated', icon: '', title: '', description: '', selected: false },
  ]
};

// 
const MY_LISTINGS: PropertyData[] = [
  DEFAULT_PROPERTY,
  {
    id: 'demo-002',
    title: '423',
    address: ' 256 ',
    district: '',
    price: 88000000,
    pricePerPing: 1120000,
    size: 78.5,
    rooms: '423',
    floor: '18F/22F',
    age: 3,
    direction: '',
    parking: '',
    managementFee: 6800,
    community: '',
    communityYear: 2021,
    communityUnits: 156,
    images: [],
    highlights: [
      { id: 'commute', icon: '', title: '', description: ' 3 ', selected: true },
      { id: 'view', icon: '', title: '', description: ' 101', selected: true },
      { id: 'community', icon: '', title: '', description: '', selected: true },
      { id: 'school', icon: '', title: '', description: '', selected: false },
      { id: 'amenity', icon: '', title: '', description: ' 5 ', selected: false },
      { id: 'parking', icon: '', title: '', description: '', selected: false },
      { id: 'renovated', icon: '', title: '', description: '', selected: false },
    ]
  },
  {
    id: 'demo-003',
    title: '211',
    address: ' 88 ',
    district: '',
    price: 24500000,
    pricePerPing: 680000,
    size: 36.0,
    rooms: '211',
    floor: '5F/12F',
    age: 15,
    direction: '',
    parking: '',
    managementFee: 2200,
    community: '',
    communityYear: 2009,
    communityUnits: 88,
    images: [],
    highlights: [
      { id: 'amenity', icon: '', title: '', description: 'SOGO', selected: true },
      { id: 'school', icon: '', title: '', description: '', selected: true },
      { id: 'community', icon: '', title: '', description: '', selected: true },
      { id: 'commute', icon: '', title: '', description: '', selected: false },
      { id: 'view', icon: '', title: '', description: '', selected: false },
      { id: 'renovated', icon: '', title: '', description: '', selected: false },
      { id: 'parking', icon: '', title: '', description: '', selected: false },
    ]
  }
];

// 
const REPORT_STYLES: { id: ReportStyle; icon: string; title: string; desc: string }[] = [
  { id: 'simple', icon: '', title: '', desc: '' },
  { id: 'investment', icon: '', title: '', desc: '' },
  { id: 'marketing', icon: '', title: '', desc: '' },
];

export default function ReportGenerator({ listings = [] }: ReportGeneratorProps) {
  const [step, setStep] = useState<1 | 2 | 3 | 4 | 5>(1);
  const [selectedProperty, setSelectedProperty] = useState<PropertyData | null>(null);
  const [selectedStyle, setSelectedStyle] = useState<ReportStyle>('simple');
  const [highlights, setHighlights] = useState<Highlight[]>([]);
  const [reportUrl, setReportUrl] = useState<string>('');
  const [isGenerating, setIsGenerating] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const formatPrice = (price: number) => {
    if (price >= 100000000) {
      return `${(price / 100000000).toFixed(2)} `;
    }
    return `${(price / 10000).toFixed(0)} `;
  };

  const calculateMonthlyPayment = (price: number, downPaymentRatio = 0.2, years = 30, rate = 0.021) => {
    const principal = price * (1 - downPaymentRatio);
    const monthlyRate = rate / 12;
    const months = years * 12;
    const payment = principal * (monthlyRate * Math.pow(1 + monthlyRate, months)) / (Math.pow(1 + monthlyRate, months) - 1);
    return Math.round(payment);
  };

  const handleSelectProperty = (property: PropertyData) => {
    setSelectedProperty(property);
    setHighlights(property.highlights);
    setStep(2);
  };

  const handleSelectStyle = (style: ReportStyle) => {
    setSelectedStyle(style);
    setStep(3);
  };

  const toggleHighlight = (id: string) => {
    const selectedCount = highlights.filter(h => h.selected).length;
    setHighlights(prev => prev.map(h => {
      if (h.id === id) {
        if (!h.selected && selectedCount >= 3) {
          notify.error(' 3 ');
          return h;
        }
        return { ...h, selected: !h.selected };
      }
      return h;
    }));
  };

  const confirmHighlights = () => {
    const selectedCount = highlights.filter(h => h.selected).length;
    if (selectedCount < 1) {
      notify.error(' 1 ');
      return;
    }
    setStep(4);
  };

  const generateReport = async () => {
    if (!selectedProperty) return;
    
    setIsGenerating(true);
    const toastId = notify.loading('...');
    
    try {
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      const reportId = `R-${Date.now().toString(36).toUpperCase()}`;
      const url = `${window.location.origin}/r/${reportId}`;
      
      setReportUrl(url);
      setStep(5);
      notify.success('', undefined, { id: toastId });
    } catch {
      notify.error('', undefined, { id: toastId });
    } finally {
      setIsGenerating(false);
    }
  };

  const copyLink = async () => {
    try {
      await navigator.clipboard.writeText(reportUrl);
      notify.success(' LINE');
    } catch {
      notify.error('');
    }
  };

  const shareToLine = () => {
    const message = encodeURIComponent(`${selectedProperty?.title}\n\n \n\n${reportUrl}`);
    window.open(`https://line.me/R/msg/text/?${message}`, '_blank');
  };

  const reset = () => {
    setStep(1);
    setSelectedProperty(null);
    setSelectedStyle('simple');
    setHighlights([]);
    setReportUrl('');
  };

  const handleImportClick = () => {
    fileInputRef.current?.click();
  };

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    const toastId = notify.loading('AI ...');

    try {
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      const detectedProperty: PropertyData = {
        id: 'detected-001',
        title: ' 18F',
        address: '',
        district: '',
        price: 188000000,
        pricePerPing: 1560000,
        size: 120.5,
        rooms: '434',
        floor: '18F/24F',
        age: 10,
        direction: '',
        parking: '',
        managementFee: 15000,
        community: '',
        communityYear: 2014,
        communityUnits: 52,
        images: [],
        highlights: [
          { id: 'view', icon: '', title: '', description: '', selected: true },
          { id: 'community', icon: '', title: '', description: '', selected: true },
          { id: 'renovated', icon: '', title: '', description: '', selected: true },
          { id: 'parking', icon: '', title: '', description: '', selected: false },
          { id: 'amenity', icon: '', title: '', description: '', selected: false },
          { id: 'commute', icon: '', title: '', description: ' 8 ', selected: false },
          { id: 'school', icon: '', title: '', description: '', selected: false },
        ]
      };
      
      setSelectedProperty(detectedProperty);
      setHighlights(detectedProperty.highlights);
      setStep(2);
      notify.success('AI ', undefined, { id: toastId });
    } catch {
      notify.error('', undefined, { id: toastId });
    }
  };

  const renderStep1 = () => (
    <div className={styles.reportStep}>
      <div className={styles.reportStepHeader}>
        <span className={styles.reportStepBadge}>1/4</span>
        <h3></h3>
      </div>
      
      <div className={styles.reportImportSection}>
        <input 
          type="file" 
          ref={fileInputRef} 
          style={{ display: 'none' }} 
          accept="image/*" 
          onChange={handleFileChange} 
        />
        <button className={styles.reportImportBtn} onClick={handleImportClick}>
          <Upload size={20} />
          <span></span>
          <small>AI </small>
        </button>
      </div>
      
      <div className={styles.reportDivider}>
        <span></span>
      </div>
      
      <div className={styles.reportListings}>
        {MY_LISTINGS.map(property => (
          <button
            key={property.id}
            className={styles.reportListingItem}
            onClick={() => handleSelectProperty(property)}
          >
            <div className={styles.reportListingThumb}>
              <Home size={24} />
            </div>
            <div className={styles.reportListingInfo}>
              <div className={styles.reportListingTitle}>{property.title}</div>
              <div className={styles.reportListingMeta}>
                <span><MapPin size={12} /> {property.district}</span>
                <span><DollarSign size={12} /> {formatPrice(property.price)}</span>
              </div>
            </div>
            <ChevronRight size={20} className={styles.reportListingArrow} />
          </button>
        ))}
      </div>
    </div>
  );

  const renderStep2 = () => (
    <div className={styles.reportStep}>
      <div className={styles.reportStepHeader}>
        <button className={styles.reportBackBtn} onClick={() => setStep(1)}>
          <ChevronLeft size={20} />
        </button>
        <span className={styles.reportStepBadge}>2/4</span>
        <h3></h3>
      </div>
      
      <div className={styles.reportSelectedProperty}>
        <Home size={18} />
        <span>{selectedProperty?.title}</span>
      </div>
      
      <div className={styles.reportStyles}>
        {REPORT_STYLES.map(style => (
          <button
            key={style.id}
            className={`${styles.reportStyleItem} ${selectedStyle === style.id ? styles.selected : ''}`}
            onClick={() => handleSelectStyle(style.id)}
          >
            <span className={styles.reportStyleIcon}>{style.icon}</span>
            <div className={styles.reportStyleInfo}>
              <div className={styles.reportStyleTitle}>{style.title}</div>
              <div className={styles.reportStyleDesc}>{style.desc}</div>
            </div>
            {selectedStyle === style.id && <CheckCircle size={20} className={styles.reportStyleCheck} />}
          </button>
        ))}
      </div>
    </div>
  );

  const renderStep3 = () => (
    <div className={styles.reportStep}>
      <div className={styles.reportStepHeader}>
        <button className={styles.reportBackBtn} onClick={() => setStep(2)}>
          <ChevronLeft size={20} />
        </button>
        <span className={styles.reportStepBadge}>3/4</span>
        <h3> 3 </h3>
      </div>
      
      <p className={styles.reportStepHint}>
        
      </p>
      
      <div className={styles.reportHighlights}>
        {highlights.map(highlight => (
          <button
            key={highlight.id}
            className={`${styles.reportHighlightItem} ${highlight.selected ? styles.selected : ''}`}
            onClick={() => toggleHighlight(highlight.id)}
          >
            <span className={styles.reportHighlightIcon}>{highlight.icon}</span>
            <div className={styles.reportHighlightInfo}>
              <div className={styles.reportHighlightTitle}>{highlight.title}</div>
              <div className={styles.reportHighlightDesc}>{highlight.description}</div>
            </div>
            <div className={styles.reportHighlightCheck}>
              {highlight.selected ? <CheckCircle size={20} /> : <div className={styles.reportHighlightUnchecked} />}
            </div>
          </button>
        ))}
      </div>
      
      <div className={styles.reportStepFooter}>
        <span> {highlights.filter(h => h.selected).length}/3</span>
        <button className={styles.reportNextBtn} onClick={confirmHighlights}>
           <ChevronRight size={18} />
        </button>
      </div>
    </div>
  );

  const renderStep4 = () => {
    if (!selectedProperty) return null;
    
    const selectedHighlights = highlights.filter(h => h.selected);
    const monthlyPayment = calculateMonthlyPayment(selectedProperty.price);
    
    return (
      <div className={styles.reportStep}>
        <div className={styles.reportStepHeader}>
          <button className={styles.reportBackBtn} onClick={() => setStep(3)}>
            <ChevronLeft size={20} />
          </button>
          <span className={styles.reportStepBadge}>4/4</span>
          <h3></h3>
        </div>
        
        <div className={styles.reportPhoneFrame}>
          <div className={styles.reportPhoneNotch} />
          <div className={styles.reportPhoneContent}>
            <div className={styles.reportPreviewHero}>
              <div className={styles.reportPreviewHeroImg}>
                <Home size={48} />
              </div>
              <div className={styles.reportPreviewHeroOverlay}>
                <div className={styles.reportPreviewPrice}>
                  NT$ {formatPrice(selectedProperty.price)}
                </div>
                <div className={styles.reportPreviewTitle}>
                  {selectedProperty.title}
                </div>
              </div>
            </div>
            
            <div className={styles.reportPreviewSpecs}>
              <div><Ruler size={14} /> {selectedProperty.size} </div>
              <div><Building size={14} /> {selectedProperty.floor}</div>
              <div><Calendar size={14} /> {selectedProperty.age} </div>
              <div><Compass size={14} /> {selectedProperty.direction}</div>
            </div>
            
            <div className={styles.reportPreviewMortgage}>
              <div className={styles.reportPreviewMortgageLabel}> 8 30 </div>
              <div className={styles.reportPreviewMortgageValue}>
                 NT$ {monthlyPayment.toLocaleString()}
              </div>
            </div>
            
            <div className={styles.reportPreviewHighlights}>
              {selectedHighlights.map(h => (
                <div key={h.id} className={styles.reportPreviewHighlight}>
                  <span>{h.icon}</span>
                  <div>
                    <strong>{h.title}</strong>
                    <p>{h.description}</p>
                  </div>
                </div>
              ))}
            </div>
            
            <div className={styles.reportPreviewAgent}>
              <div className={styles.reportPreviewAgentAvatar}></div>
              <div className={styles.reportPreviewAgentInfo}>
                <strong></strong>
                <span>MaiHouses </span>
              </div>
            </div>
          </div>
        </div>
        
        <button 
          className={styles.reportGenerateBtn}
          onClick={generateReport}
          disabled={isGenerating}
        >
          {isGenerating ? (
            <> ...</>
          ) : (
            <><Sparkles size={18} /> </>
          )}
        </button>
      </div>
    );
  };

  const renderStep5 = () => (
    <div className={styles.reportStep}>
      <div className={styles.reportStepHeader}>
        <span className={styles.reportStepBadge}></span>
        <h3></h3>
      </div>
      
      <div className={styles.reportSuccess}>
        <div className={styles.reportSuccessIcon}></div>
        <p></p>
      </div>
      
      <div className={styles.reportUrlBox}>
        <input 
          type="text" 
          value={reportUrl} 
          readOnly 
          className={styles.reportUrlInput}
        />
        <button className={styles.reportUrlCopy} onClick={copyLink}>
          <Copy size={18} />
        </button>
      </div>
      
      <div className={styles.reportShareButtons}>
        <button className={styles.reportShareBtnPrimary} onClick={copyLink}>
          <Copy size={18} />
          
        </button>
        <button className={styles.reportShareBtnLine} onClick={shareToLine}>
          <MessageCircle size={18} />
          LINE 
        </button>
      </div>
      
      <button 
        className={styles.reportPreviewLink}
        onClick={() => window.open(reportUrl, '_blank')}
      >
        <ExternalLink size={16} />
        
      </button>
      
      <button className={styles.reportResetBtn} onClick={reset}>
        
      </button>
    </div>
  );

  return (
    <section className={`${uagStyles['uag-card']} ${uagStyles['k-span-3']}`}>
      <div className={uagStyles['uag-card-header']}>
        <div>
          <div className={uagStyles['uag-card-title']}> </div>
          <div className={uagStyles['uag-card-sub']}> Word </div>
        </div>
        {step > 1 && step < 5 && (
          <button 
            className={`${uagStyles['uag-btn']} ${uagStyles['secondary']}`}
            onClick={reset}
          >
            <X size={14} /> 
          </button>
        )}
      </div>
      
      <div className={styles.reportContainer}>
        {step === 1 && renderStep1()}
        {step === 2 && renderStep2()}
        {step === 3 && renderStep3()}
        {step === 4 && renderStep4()}
        {step === 5 && renderStep5()}
      </div>
    </section>
  );
}
