// ============================================
//   Prompt
// ============================================

export function buildEnhancedPrompt(
  userState: UserState,
  emotionalState: DetailedEmotionalState,
  recommendationPhase: RecommendationPhase,
  topCategory: TagCategory | null,
  chitchatRounds: number,
  messageStyle: 'brief' | 'expressive' | 'neutral'
): string {
  let prompt = MAIMAI_SYSTEM_PROMPT;
  
  // ============================================
  // 
  // ============================================
  const stateGuide = {
    'exploring': `\n\n 

- 
- 
- `,
    
    'semi-warm': `\n\n 
${topCategory || ''}
- 
- 
- `,
    
    'explicit': `\n\n 

- 
- 
- `
  }[userState];
  
  prompt += stateGuide;
  
  // ============================================
  // 
  // ============================================
  if (emotionalState === 'storytelling') {
    prompt += `\n\n 
`;
  } else if (emotionalState === 'frustrated') {
    prompt += `\n\n 


`;
  } else if (emotionalState === 'stressed') {
    prompt += `\n\n 


`;
  }
  
  // ============================================
  // 
  // ============================================
  switch (recommendationPhase) {
    case 'seed':
      const anchorQ = pickLifeAnchorQuestion();
      prompt += `\n\n 
 ${chitchatRounds} 
${anchorQ}
`;
      break;
      
    case 'pave':
      //  
      const bridgeTopic = getCurrentBridgeTopic();
      if (bridgeTopic) {
        prompt += `\n\n 
${bridgeTopic.script.topic}

${bridgeTopic.script.bridge}

${bridgeTopic.script.feature}
 `;
      } else if (topCategory) {
        const community = getCommunityByCategory(topCategory);
        prompt += `\n\n 
${topCategory}
${community.pavePhrase}
 `;
      }
      break;
      
    case 'card':
      if (topCategory) {
        const community = getCommunityByCategory(topCategory);
        prompt += `\n\n 
${topCategory}

[[:${community.name}:${community.topic}]]`;
      }
      break;
      
    case 'listing':
      prompt += `\n\n 


[[::MH-2024-001]]`;
      break;
      
    default:
      prompt += `\n\n 
`;
  }
  
  // ============================================
  // 
  // ============================================
  const styleHint = {
    brief: '\n\n1-2',
    expressive: '\n\n',
    neutral: ''
  }[messageStyle];
  
  if (styleHint) {
    prompt += styleHint;
  }
  
  return prompt;
}

