import React from 'react';
import styles from '../UAG.module.css';

export const UAGHeader = () => (
  <header className={styles['uag-header']}>
    <div className={styles['uag-header-inner']}>
      <div className={styles['uag-logo']}><div className={styles['uag-logo-badge']}></div><span></span></div>
      <div className={styles['uag-breadcrumb']}><span> 21</span><span className={`${styles['uag-badge']} ${styles['uag-badge--pro']}`}> PRO</span></div>
    </div>
  </header>
);
